.project {
  width: 30%;
  height: 40vh;
  margin: 20px 10px;

  border: 2px solid rgb(243, 242, 242);

  border-radius: 30px 30px 0px 0px;

 /*  border: 2px solid #303b60; */


  overflow: hidden;
}

.project-img {
  width: 100%;

  transition: all 12s ease;
}

.project:hover .project-img {
  transform: translateY(-88%);
}

.project-name {
  background-color: white;
  position: sticky;

  font-weight: 300;

  z-index: 2;
}

